import { REBATES_DOMAIN_FULL_ADDRESS, CAMPAIGN_MEASURMENT_PARAM, REBATES_IMAGES_DOMAIN } from '../../../../utilities/src/rebates_const'
import PopupHelper from '../../../../utilities/src/popupHelper'

export default class CampaignDisplayList{
    constructor(){
        this.campaigns=[];
    }

    generateCampaignDisplayList(campaignInfo){
        if(campaignInfo.campaignCoupons!=null && campaignInfo.campaignCoupons != undefined){
            for(let i=0; i<campaignInfo.campaignCoupons.length; i++) {
                let selectedStore = campaignInfo.couponStores.find((obj)=>{return obj.id === campaignInfo.campaignCoupons[i].storeId});
                if(selectedStore != null){
                    let campaignDisp = new CampaignDisplay();
                    campaignDisp.setStoreImageLogo(selectedStore.smallLogo);
                    campaignDisp.setstoreImageLink(selectedStore.urlName);
                    campaignDisp.setstoreName(selectedStore.storeName, selectedStore.jp_ktn_storeName, selectedStore.jp_hgn_storeName);
                    campaignDisp.setstoreTooltip(selectedStore.cashBackText);
                    campaignDisp.setstoreCouponUrl(campaignInfo.campaignLink, campaignInfo.campaignCoupons[i].id);
                    campaignDisp.setDescription(campaignInfo.campaignCoupons[i].description);
                    campaignDisp.setCashBackText(selectedStore.cashBack, selectedStore.isFixedCashBack);
                    campaignDisp.setExpiry(campaignInfo.campaignCoupons[i].expires);
                    this.campaigns.push(campaignDisp);
                }
            }
        }
    }
}


class CampaignDisplay{
    constructor(){
        this.storeImageLogo = '';
        this.storeImageLink = '';
        this.storeName = '';
        this.storeTooltip = '';
        this.storeCouponUrl ='';
        this.cashBackText='';
        this.description = '';
        this.expiry = '';
    }

    setStoreImageLogo(path){
        if(path != null && path !== undefined && path !='') {
            this.storeImageLogo = `${REBATES_IMAGES_DOMAIN}${path}`;
        }
        else{
            this.storeImageLogo = 'img/noImage_100_27.png';
        }
    }
    setstoreImageLink(urlname){
        if(urlname != null && urlname !== undefined && urlname !='') {
            this.storeImageLink = `${REBATES_DOMAIN_FULL_ADDRESS}/coupons/${urlname}${CAMPAIGN_MEASURMENT_PARAM}`;
        }
    }
    setstoreName(name, ktknName, hrgnName){
        if(name != null && name !== undefined && name !=''){
            this.storeName = name;
        }
        else if(ktknName != null && ktknName !== undefined && ktknName !=''){
            this.storeName = ktknName;
        }
        else if(hrgnName != null && hrgnName !== undefined && hrgnName !=''){
            this.storeName = hrgnName;
        }
    }
    setstoreTooltip(cashbackText){
        this.storeTooltip = `Rebates${this.storeName}${cashbackText}`
    }
    setstoreCouponUrl(url, campaignId){
        this.storeCouponUrl = `${REBATES_DOMAIN_FULL_ADDRESS}/shopping/campaign/7/coupon/${campaignId}${CAMPAIGN_MEASURMENT_PARAM}`
    }
    setDescription(description){
        this.description = `${description}`
    }
    setCashBackText(cb, isFxdCb){
        if(!isFxdCb){
            this.cashBackText = ''+Math.round(cb*1000)/10+'% ';
        }
        else{
            this.cashBackText = Math.round(cb*1000)/10+'% ';
        }

    }
    setExpiry(expDate){
        this.expiry = new PopupHelper().formatDateToYYYYMMDD(new Date(expDate*1000));
    }
}