import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { TOP_PAGE_URL_AFTER_ACTIVATE_BANNER, TOP_PAGE_URL_BEFORE_ACTIVATE_BANNER } from '../../../../../utilities/src/rebates_const'
import PopupHelper from '../../../../../utilities/src/popupHelper'

export default class RebatesImage extends Component {

    constructor(props) {
        super(props);
        this.extensionHelper = new PopupHelper();
    }

    componentDidMount() {
        let imgUrl = document.getElementById('imgURL');
        if (imgUrl) {
            imgUrl.addEventListener('click', () => {
                const top_page_url = this.props.activated ? TOP_PAGE_URL_AFTER_ACTIVATE_BANNER : TOP_PAGE_URL_BEFORE_ACTIVATE_BANNER;
                this.extensionHelper.openWindowClosePopup(top_page_url, '');
            });
        }
    }

    render() {
        const imgStyle = { "cursor" : "pointer" }
        return (
            <img id="imgURL" style={imgStyle} src="https://image.infoseek.rakuten.co.jp/content/toolbar/parts/rebates_logo.png" width={87} height={24} />
        );
    }
}

RebatesImage.propTypes = {
    activated: PropTypes.bool
};