import React from 'react';
import { Card, Modal} from '@material-ui/core';
import UserDetailsMenu from './UserDetailsMenu';
import UserDetailsHeader from './UserDetailsHeader';
import UserDetailsMemberRank from './UserDetailsMemberRank';
import UserDetailsPointInfo from './UserDetailsPointInfo';
import PropTypes from 'prop-types';
import Login from '../Login';

function UserDetails(props) {
    return (
      <Modal open={props.isOpen} onClose={props.handleClose} className='user-details'>
          <Card sx={{ minWidth: 200 }}>
              <UserDetailsHeader handleClose={props.handleClose} />
              {props.login && <UserDetailsMemberRank user_pointInfo={props.user_pointInfo} />}
              {props.login && <UserDetailsPointInfo
                user_pointInfo={props.user_pointInfo}
                user_yamawakeInfo={props.user_yamawakeInfo}
                user_kuchisuInfo={props.user_kuchisuInfo} />}
              {!props.login && <Login location='popup' style={{ display: 'flex', borderTop: 'unset', padding: 'unset' }}/>}
              <UserDetailsMenu login={props.login} />
          </Card>
      </Modal>
    );
}

UserDetails.propTypes = {
    user_pointInfo: PropTypes.object,
    user_yamawakeInfo: PropTypes.string,
    user_kuchisuInfo: PropTypes.string,
    login: PropTypes.bool,
    isOpen: PropTypes.bool,
    handleClose: PropTypes.func,
};

export default UserDetails;
