function renderPopUpContent() {
	const contentDivElem = document.querySelector('.contentDiv');
	const contentDivTop = contentDivElem.getBoundingClientRect().top;
	const footerHeight = getAbsoluteHeightWithMargin(
                             document.getElementById('footer_contents'));

	const newMaxHeight = CHROME_EXT_MAX_HEIGHT - contentDivTop - footerHeight;
	for (let nextContentDivElem of document.querySelectorAll('.contentDiv')) {
		setAbsoluteHeight(nextContentDivElem, newMaxHeight);
    }
}

function getAbsoluteHeightWithMargin(el) {
    const styles = window.getComputedStyle(el);
    if (!styles) {
        return 0;
    }
    const height = parseFloat(styles.height);
    const margin = parseFloat(styles.marginTop) +
                   parseFloat(styles.marginBottom);
    return height + margin;
}

function setAbsoluteHeight(el, outerHeight) {
    let height = outerHeight;

    const styles = window.getComputedStyle(el);
    if (styles) {
        const padding = parseFloat(styles.paddingTop) +
                        parseFloat(styles.paddingBottom);
        if (padding) {
            height -= padding;
        }

        const border = parseFloat(styles.borderTop) +
                       parseFloat(styles.borderBottom);
        if (border) {
            height -= border;
        }
    }

    el.style.height = height  + "px";
}
