import React, {Component} from 'react';
import PropTypes from 'prop-types';
import RebatesActivate from './RebatesActivate';
import RebatesNonActivate from './RebatesNonActivate';


export default class Rebates extends Component {

  constructor(props) {
      super(props);
      this.state = {
          shoppingURL: this.props.rebates_info.link,
          comment: this.props.rebates_info.comment,
          activated: this.props.activated,
          domainRegex : this.props.rebates_info.domainRegex
      };
  }

componentDidMount() {
     let shopUrl = document.getElementById('shopURL');
     if(shopUrl && this.state.domainRegex != ''){
            shopUrl.addEventListener('click', () => {
            this.props.onClick();
         });
    }
}

  render() {
    return (
      <div>
        { this.state.activated ?
        <RebatesActivate rebates_info={this.props.rebates_info} />
         :
        <RebatesNonActivate onClick={this.props.onClick} rebates_info={this.props.rebates_info} />
        }
      </div>
    );
  }
}

Rebates.propTypes = {
  rebates_info: PropTypes.object,
  activated: PropTypes.bool,
  onClick: PropTypes.func
};