import React, {Component} from 'react';
import PopupHelper from '../../../../../../utilities/src/popupHelper';
import {REBATES_DOMAIN_FULL_ADDRESS} from '../../../../../../utilities/src/rebates_const';

export default class RebatesCampaignNoInfo extends Component {
    constructor(props){
        super(props);
        this.extensionHelper = new PopupHelper();
        this.handleClick=this.handleClick.bind(this);
    }

    handleClick(){
        this.extensionHelper.openWindowClosePopup(REBATES_DOMAIN_FULL_ADDRESS, '');
    }
    render(){
        let Styles = {
            divStyle:{
                position:"relative",
                top:"20%"
            }
        }
        return(<div id="error" style={Styles.divStyle}><p><img src="img/ic_error.png" width="19" height="19" alt="" />
                <a href="#" onClick={this.handleClick}>Rebates</a></p>
               </div>);
    }
}