import React, { Component } from 'react';
import PropTypes from 'prop-types';
import {
    REBATES_DEFAULT_STORE_EXCLUSION,
    REBATES_DOMAIN_FULL_ADDRESS,
    STATIC_GUIDE_URL
} from '../../../../../utilities/src/rebates_const'
import  PopupHelper from  '../../../../../utilities/src/popupHelper'

export default class WhatIsRebates extends Component {
    constructor(props) {
        super(props);
        this.extensionHelper = new PopupHelper();
    }

    componentDidMount() {
        document.getElementById('WhatIsRebatesLinkImage').addEventListener('click', () => {
            this.extensionHelper.openWindowClosePopup(STATIC_GUIDE_URL, '');
        });
        document.getElementById('WhatIsRebatesLinkAnchor').addEventListener('click', () => {
            this.extensionHelper.openWindowClosePopup(STATIC_GUIDE_URL, '');
        });
    }

    render() {
        //- Get exclusion page url
        let exclusionUrl = this.getExclusionUrl();

        const linkStyle = {
            "textAlign": "right",
            "fontSize": "13px",
            "verticalAlign": "middle",
            "margin": "0",
            "padding": "0 5px 0 0"
        }

        const textStyle = {
            "color": "#333",
            "cursor": "pointer"
        }

        const imgStyle = {
            "verticalAlign": "middle",
            "paddingRight": "2px",
            "cursor": "pointer"
        }

        const messageStyle = {
            "display": "inline-block",
            "float": "left",
            "fontSize": "10px",
            "marginTop": "2px"
        }

        const messageHighlightStyle ={
            "textDecoration": "underline",
            "color": "#333"
        }

        return (
            <p id="WhatIsRebatesLink" style={linkStyle}>
            <span style={messageStyle}><a  target="_blank" rel="nofollow noopener noreferrer" href={exclusionUrl} style={messageHighlightStyle}> </a> </span>
            <img id="WhatIsRebatesLinkImage" src="https://image.infoseek.rakuten.co.jp/content/toolbar/parts/rebates_ic_small.png" style= {imgStyle} />
            <a id="WhatIsRebatesLinkAnchor" style={textStyle}>Rebates</a>
            </p>
        );
    }
    getExclusionUrl() {
        let urlExclusion = (this.props.rebates_info.urlName !== '') ? `${this.props.rebates_info.urlName}/#terms` : REBATES_DEFAULT_STORE_EXCLUSION;
        return `${REBATES_DOMAIN_FULL_ADDRESS}/${urlExclusion}`;
    }
}

WhatIsRebates.propTypes = {
    rebates_info: PropTypes.object,
    GoToWhatIsRebates: PropTypes.func
}