import React, {Component} from 'react';
import PropTypes from 'prop-types';
import RebatesCoupon from './RebatesCoupon';
import {REBATES_DOMAIN_FULL_ADDRESS} from '../../../../../../utilities/src/rebates_const';
import PopupHelper from '../../../../../../utilities/src/popupHelper';


export default class RebatesCampaignContent extends Component {
    constructor(props){
        super(props);
        this.state = {campaigns:this.props.campaigns};
        this.extensionHelper = new PopupHelper();
        this.handleRebatesClick=this.handleRebatesClick.bind(this);
    }

    handleRebatesClick(e){
        this.extensionHelper.openWindowClosePopup(REBATES_DOMAIN_FULL_ADDRESS, '');
    }

    render() {
        return (
          <div>
            <div className="row">
              <div id="rebates">
                <h3>
                  <img
                    id="point_icon"
                    width="18"
                    height="18"
                    src={"img/point.png"}
                    alt="Rebates"
                    style={{
                      display: "inline-block",
                      marginLeft: "10px",
                      marginRight: "8px",
                      verticalAlign: "middle",
                    }}
                  />
                  Rebates 
                </h3>
              </div>
              <div>
                <div className="text-right rebates_link">
                  <a href="#" onClick={this.handleRebatesClick}>
                    
                  </a>
                </div>
              </div>
            </div>
            {this.state.campaigns.map((campaignDisplay, i) => (
              <RebatesCoupon key={i} campaignDisplay={campaignDisplay} />
            ))}
          </div>
        );
      }
}

RebatesCampaignContent.propTypes = {
    campaigns:PropTypes.array
}


