export default class PopupHelper{
    constructor(){

    }

    openWindow(url, name){
        platform_api_root.tabs.create({active: true, url: url});
    }

    closePopup(){
        window.close();
    }

    openWindowClosePopup(url, name){
        setTimeout(() => {
            this.openWindow(url, name);
            this.closePopup();
        }, 100); // since google analytics cannot send the msg to background,, we will have this timer here
    }

    formatDateToYYYYMMDD(input){
        let formattedDate = '';
        if(input!=null && input != undefined && input != ''){
            let mm = input.getMonth() + 1; // getMonth() is zero-based
            let dd = input.getDate();

            formattedDate = [input.getFullYear(), '/',
                    (mm>9 ? '' : '0') + mm, '/',
                    (dd>9 ? '' : '0') + dd
                    ].join('');
        }
        return formattedDate;
    }

    shortenText(text, limitlen) {
        if (limitlen < 3) {
          limitlen = 3;
        }
        return text.length <= limitlen ? text
                                       : text.substring(0, limitlen - 3).concat('...');
    }
}