import React, { Component } from 'react';
import PropTypes from 'prop-types';
import  { CONST_RAKUTEN_WEBSEARCH } from  '../../../../../utilities/src/setting_config';
import PopupHelper from '../../../../../utilities/src/popupHelper';
import UserDetails from './popuphover/UserDetails';
import Login from './Login';
import UserPoint from '../../../../../utilities/src/user_point'
import {Store} from 'react-chrome-redux';

const proxyStore = new Store({ portName: 'background_data', extensionId: null });

export default class Header extends Component {

  constructor(props) {
    super(props);
    this.extensionHelper = new PopupHelper();

    // Set default values
    let usrPointInfo = new UserPoint();
    usrPointInfo.name = '';
    usrPointInfo.totalPoint = 0;
    usrPointInfo.termPoint = 0;
    usrPointInfo.cash = "0";
    usrPointInfo.futureGrantedPoint = 0;
    usrPointInfo.rank = '';
    usrPointInfo.monthlyPoint = 0;
    usrPointInfo.yesterdayPoint = 0;

    this.state = {
      isModalOpen: false,
      userPointInfo: usrPointInfo,
    }
  }

  componentDidMount() {
    // header
    document.getElementById('header_homepage_link').addEventListener('click', () => {
      this.extensionHelper.openWindowClosePopup(`${CONST_RAKUTEN_WEBSEARCH.defaultUrl}`, 'homeWin');
    });
  }

  async loadUserData() {
    if (proxyStore.getState().data.login != 1) {
      return;
    }

    function formatDate(date) {
      const year = date.getFullYear();
      const month = String(date.getMonth() + 1).padStart(2, '0');
      const day = String(date.getDate()).padStart(2, '0');
      return `${year}-${month}-${day}`;
    }

    // get user point info
    function generateMonthlyPointURL() {
      const currentDate = new Date();
      const firstDayOfMonth = new Date(currentDate.getFullYear(), currentDate.getMonth(), 1);
      const formattedStartDate = formatDate(firstDayOfMonth);
      const formattedEndDate = formatDate(currentDate);

      const baseURL = `${CONST_RAKUTEN_WEBSEARCH.api}/json/userpointhistory`;
      const encodedStartDate = encodeURIComponent(`${formattedStartDate} 00:00:00`);
      const encodedEndDate = encodeURIComponent(`${formattedEndDate} 00:00:00`);

      return `${baseURL}?start_date=${encodedStartDate}&end_date=${encodedEndDate}`;
    }

    const [response_user, response_point] = await Promise.all([
      fetch(`${CONST_RAKUTEN_WEBSEARCH.defaultUrl}/json/user`),
      fetch(generateMonthlyPointURL())
    ]);

    if (response_user && response_user.status === 200) {
      const userResult = await response_user.json();
      if (userResult.status === "true") {
        const pointResult = await response_point.json();
        const userPointInfo = setUserPointInfo(userResult, pointResult);
        this.setState({
          userPointInfo
        });
      }
    }

    function setUserPointInfo(result_user, result_historypoint) {
      function getTotalPoints(jsonData) {
        if (!jsonData || !jsonData.pointInfo) {
          return 0;
        }
        const pointInfo = jsonData.pointInfo;
        return pointInfo.reduce((accumulator, currentItem) => {
          return accumulator + currentItem.point;
        }, 0);
      }

      function getYesterdayPoint(jsonData) {
        if (!jsonData || !jsonData.pointInfo) {
          return 0;
        }
        const today = new Date();
        const yesterday = new Date(today);
        yesterday.setDate(today.getDate() - 1);
        const yesterdayFormatted = yesterday.toISOString().split('T')[0];
        for (const pointInfo of jsonData.pointInfo) {
          if (pointInfo.pointDate === yesterdayFormatted) {
            return pointInfo.point;
          }
        }
        return 0;
      }

      let usrPointInfo = new UserPoint();
      usrPointInfo.name = result_user.your_name;
      usrPointInfo.totalPoint = result_user.yourUsablePoint;
      usrPointInfo.termPoint = result_user.your_point_tmp;
      usrPointInfo.cash = "0";
      usrPointInfo.futureGrantedPoint = result_user.futureGrantedPoint;
      usrPointInfo.rank = result_user.rank;
      usrPointInfo.monthlyPoint = getTotalPoints(result_historypoint);
      usrPointInfo.yesterdayPoint = getYesterdayPoint(result_historypoint);
      return usrPointInfo;
    }
  }

  toggleModal() {
    this.setState({ isModalOpen: !this.state.isModalOpen });
    if (this.state.isModalOpen === false) {
      void this.loadUserData();
    }
  }

  render() {
    let styles = {
      divParent :{
        display: 'flex',
        position: 'relative',
        flexDirection: 'row',
        justifyContent: 'center',
        alignItems: 'center',
        marginBottom: '0',
        marginTop: '5px',
        marginLeft: '2px',
        marginRight: '2px',
        cursor: 'pointer',
      },
      menuImg: {
        width: '37px',
        height: '39px',
      },
      logoImg: {
        width: '170px',
        height: '24.39px'
      }
    }
    return (
      <React.Fragment>
        <div id="header" style={styles.divParent}>
          <div style={{ position: 'absolute', top: 0, left: '5px' }} onClick={() => this.toggleModal()}>
            <img id="header_menu_link" src={"img/menu.png"} alt="Embedded Image" style={styles.menuImg} />
          </div>
          <div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center', padding: '5px 0' }}>
            <img id="header_homepage_link" src={"img/rws_logo.png"} alt="Embedded Image" style={styles.logoImg} />
          </div>
          {this.state.userPointInfo && <UserDetails
            isOpen={this.state.isModalOpen}
            handleClose={() => this.toggleModal()}
            login={this.props.login}
            user_pointInfo={this.state.userPointInfo}
            user_yamawakeInfo={this.props.yamawakeInfo}
            user_kuchisuInfo={this.props.KuchisuInfo}
          />}
        </div>

        <div>
          <Login />
        </div>
      </React.Fragment>
    );
  }
}

Header.propTypes = {
  login: PropTypes.bool,
  yamawakeInfo: PropTypes.string,
  KuchisuInfo: PropTypes.string,
};
