import React, { Component } from 'react';
import PropTypes from 'prop-types';
import Rebates from './Rebates';
import  { RebatesInfo } from  '../../../../../utilities/src/rebates_const'
import  { BANNER_DISABLE_OPTION } from  '../../../../../utilities/src/setting_config'
import PopupHelper from '../../../../../utilities/src/popupHelper'

export default class Footer extends Component {

  constructor(props) {
    super(props);
    this.state = {
      rebates_info: this.props.rebates_info == null ? new RebatesInfo() : this.props.rebates_info,
      login: this.props.login,
      activated: this.props.activated,
    };
    this.extensionHelper = new PopupHelper();
  }

  render() {
    return (
      <div id="footer">
        {
          this.props.banner_disable_option == BANNER_DISABLE_OPTION.NO_USE ? null :
          <Rebates onClick={this.props.onClick} rebates_info={this.state.rebates_info} activated={this.state.activated} />
        }
      </div>
    );
  }
}

Footer.propTypes = {
    banner_disable_option: PropTypes.number,
    rebates_info: PropTypes.object,
    login: PropTypes.bool,
    activated: PropTypes.bool,
    onClick: PropTypes.func,
};
