import React from 'react'
import { makeStyles } from '@material-ui/core/styles'
import { Typography } from '@material-ui/core'
import * as PropTypes from 'prop-types'

const useStyles = makeStyles({
  runaAdContainer: {
    position: 'relative',
    width: '100%',
    paddingTop: '56.25%',
    alignItems: 'center',
    backgroundColor: '#fff',
  },
  adIframe: {
    position: 'absolute',
    top: '0',
    left: '0',
    bottom: '0',
    right: '0',
    margin: '0px',
    padding: '0px',
    width: '100%',
    height: '100%',
    border: 'none',
  },
  titleContainer: {
    display: 'flex',
    alignItems: 'center',
    gap: '0.5rem',
    marginLeft: '0.7rem', // Add margin to the top
  },
  titleIcon: {
    fontSize: '1rem',
  },
  rttIcon: {
    width: '18px',
    height: '18px',
  },
  title: {
    fontSize: '0.8rem',
    fontWeight: 'bold',
    fontFamily: `"Noto Sans JP", "Hiragino Kaku Gothic ProN", Meiryo, sans-serif`,
  },
})
// eslint-disable-next-line no-empty-pattern
const RunaAdContainer = ({keyProp, endpoint}) => {
  const classes = useStyles()

  const baseUrl = endpoint ? endpoint : 'https://ads-media.websearch.rakuten.co.jp'
  const url = baseUrl + '/getpriorityads?adSlot=rwsweb-pc-extensioncontents&query=null'

  return (
    <div>
      <div className={classes.titleContainer}>
        <img id="rtt_icon" className={classes.rttIcon} src={'img/rtt.png'} alt="Embedded Image"/>
        <Typography className={classes.title}></Typography>
      </div>
      {/* modify the source to use admediator*/}
      <div className={classes.runaAdContainer} key={keyProp}>
        <iframe id="runaAdIframe" className={classes.adIframe}
                src={`${url}`}></iframe>
      </div>
    </div>
  )
}

RunaAdContainer.propTypes = {
  keyProp: PropTypes.string.isRequired,
  endpoint: PropTypes.string,
}

export default RunaAdContainer
