import React from 'react';
import PropTypes from 'prop-types';

function UserDetailsHeader(props) {
  const style = {
    height: '37px',
    width: '100%',
    backgroundColor: '#00a0f0',
    position: 'relative',
  };

  const imageStyle = {
    position: 'absolute',
    top: 0,
    right: 0,
    height: '60%', // 40% smaller than original height
    width: 'auto', // this will scale the width proportionally
    padding: '7px', // add 5 pixels of padding to all sides of the image
    cursor: 'pointer', // add cursor pointer to the image to indicate it is clickable
  };

  const handleClose = () => {
    props.handleClose();
  };

  return (
    <div style={style}>
      <img src="img/close.png" style={imageStyle} alt="Close" onClick={handleClose} />
    </div>
  );
}

UserDetailsHeader.propTypes = {
  handleClose: PropTypes.func.isRequired,
};

export default UserDetailsHeader;