import React, { Component } from 'react';
import PropTypes from 'prop-types';
import RebatesImage from './RebatesImage';
import WhatIsRebates from './WhatIsRebates';

export default class RebatesActivate extends Component {

    constructor(props) {
        super(props);
        this.state = {
            shoppingURL: this.props.rebates_info.link,
            comment: this.props.rebates_info.comment,
        };
    }

    render() {
        let rbFontSize = "14px";
        if(!this.props.rebates_info.fixedCashBack){
            rbFontSize ="13px";
        }

        const styles = {
            bannerButtonStyle: {
                'fontSize': rbFontSize
            }
        }
        return (
            <div>
            <hr />
            <div className="RebatesMerchantsSite">
                <RebatesImage activated={true} />
                {/* we removed {this.state.comment} by the request of Rebates. */}
                <div style={styles.bannerButtonStyle} className="RebatesMerchantsSiteAfterActivateDiv"></div>
            </div>
            <WhatIsRebates rebates_info={this.props.rebates_info} />
            </div>
        );
    }
}

RebatesActivate.propTypes = {
    rebates_info: PropTypes.object
};