import React, {Component} from 'react';
import RebatesCampaignNoInfo from './RebatesCampaign/RebatesCampaignNoInfo'
import RebatesCampaignContent from './RebatesCampaign/RebatesCampaignContent'
import CampaignDisplayList from '../../views/CampaignDisplay';
import RunaAdContainer from './RunaAdContainer';
import PropTypes from 'prop-types';
import { CONST_RAKUTEN_WEBSEARCH } from '../../../../../utilities/src/setting_config'
import PopupHelper from '../../../../../utilities/src/popupHelper'
import {Store} from 'react-chrome-redux';

const proxyStore = new Store({ portName: 'background_data', extensionId: null });

export default class RakutenTabs extends Component {
    constructor(props) {
        super(props);
        this.state = {
            tabLabelValue: 1,
            value: 1,
            runaKeyReload: 0,
            campaignInfoExists: false,
        };
        this.onTabSelectionChanged = this.onTabSelectionChanged.bind(this);
    }

    onTabSelectionChanged(event, value) {
        if (value === 2) {
            if (this.state.value === 1) {
                this.setState({
                    tabLabelValue: value
                });
                this.setState({ value: value });
                this.setState({ runaKeyReload: this.state.runaKeyReload + 1 });
            }

            if (!this.state.campaignInfoExists) {
                platform_api_root.runtime.sendMessage({
                    method: "getRebatesCampaignList"
                }, (response) => {
                    if (response) {
                        const campList = new CampaignDisplayList();
                        campList.generateCampaignDisplayList(response);
                        if (campList.campaigns && campList.campaigns.length > 0) {
                            this.campaignInfo = campList.campaigns;
                            this.setState({ campaignInfoExists: true });
                        }
                    }
                });
            }
        } else {
            this.setState({ value: value });
            this.setState({ tabLabelValue: value });
        }
    }

    render() {
        const styles = {
            serpFrameStyle: {
                 width: "330px",
                 border: "none",
                 margin: "0px",
                 padding: "0px"
            },
            dfpadFrameStyle: {
                 border: "none",
                 width: "300px",
                 height: "0px",		// it will be set dynamically when iframe content is loaded.
                 marginTop: "0px",
                 marginBottom: "0px",
                 marginLeft: "10px",
                 marginRight: "10px",
                 padding: "0px"
            },
        };

        const TabPanelStyleForValue = (value) => {
            let style = {
                width: "100%",
                position: "relative",
            };
            if (this.state.value !== value) {
                style.height = "0px";
                style.overflow = "hidden";
            }
            return style;
        };

        const tabContainerStyle = {
            display: 'flex',
            flexDirection: 'row',
            justifyContent: 'center',
            alignItems: 'center',
            margin: '0 10px'
        }

        const tabItemStyle = {
            width: '100%',
            textAlign: 'center',
            background: '#00A0F0',
            padding: '8px 0',
            color: '#fff',
            borderTopRightRadius: '5px',
            borderTopLeftRadius: '5px',
            cursor: 'pointer',
            fontSize: '12px',
            fontWeight: 'bold',
        }

        const tabItemStyleInactive = {
            ...tabItemStyle,
            color: '#000', background: '#E9F8FF',
            fontWeight: 'unset',
        }

        return (
            <div id="tabRoot">
                <div style={tabContainerStyle}>
                    <div style={this.state.tabLabelValue === 1 ? tabItemStyle : tabItemStyleInactive} onClick={() => this.onTabSelectionChanged(null, 1)}>
                        
                    </div>
                    <div style={this.state.tabLabelValue === 2 ? tabItemStyle : tabItemStyleInactive} onClick={() => this.onTabSelectionChanged(null, 2)}>
                        
                    </div>
                </div>
                <div style={{ width: '100%', height: '3px', background: '#00A0F0' }}/>
                <div style={TabPanelStyleForValue(1)}>
                    <div id="box2content1" className="contentDiv">
                        <BoxMenu/>
                        <hr/>
                         <div id="iframe_pickup">
                            <iframe seamless frameBorder="0" scrolling="no" id="pickup_content"></iframe>
                        </div>
                        <div id="iframe_part">
                            <iframe style={styles.serpFrameStyle} seamless frameBorder="0" scrolling="yes" src="/img/ic_loading.gif" id="content"></iframe>
                        </div>
                        {/*<div id="iframe_dfpad">
                            <iframe style={styles.dfpadFrameStyle} seamless frameBorder="0" scrolling="no" id="dfpad"></iframe>
                        </div>*/}
                    </div>
                </div>
                <div style={TabPanelStyleForValue(2)}>
                    <div id="box2content2" className="contentDiv" style={{ overflowY: 'auto' }}>
                        {this.state.value === 2 &&
                          <RunaAdContainer keyProp={this.state.runaKeyReload}
                                                                    endpoint={proxyStore.getState().data.ExtensionSetting.RakutenBtaEndpoint}/>}
                        {this.state.campaignInfoExists
                             ? <div id="rebatesCampaign"><RebatesCampaignContent campaigns={this.campaignInfo} /></div>
                             : <RebatesCampaignNoInfo />}
                    </div>
                </div>
            </div>
        );
    }
}

const extensionHelper = new PopupHelper();
function BoxMenu() {
    const clickDoublePointLink = (category) => {
        let link;
        switch (category) {
            case "home":
                link = `${CONST_RAKUTEN_WEBSEARCH.defaultUrl}/?scid=we_r_MV3_home`;
                break;
            case "ichiba":
                link = `${CONST_RAKUTEN_WEBSEARCH.defaultUrl}/dpr?btnType=ichiba&platform=unifiedext`;
                break;
            case "kago":
                link = `${CONST_RAKUTEN_WEBSEARCH.defaultUrl}/dpr?btnType=kago&platform=unifiedext`;
                break;
            case "campaign":
                link = 'https://websearch.rakuten.co.jp/?scid=wi_r_extention_campaign&pointTab=5#section-points';
                break;
            case "coupon":
                link = `${CONST_RAKUTEN_WEBSEARCH.defaultUrl}/dpr?btnType=coupon&platform=unifiedext`;
                break;
            case "bookmark":
                link = `${CONST_RAKUTEN_WEBSEARCH.defaultUrl}/dpr?btnType=bookmark&platform=unifiedext`;
                break;
            case "purchase_history":
                link = `${CONST_RAKUTEN_WEBSEARCH.defaultUrl}/dpr?btnType=purchase_history&platform=unifiedext`;
                break;
            case "view_history":
                link = `${CONST_RAKUTEN_WEBSEARCH.defaultUrl}/dpr?btnType=view_history&platform=unifiedext`;
                break;
        }
        extensionHelper.openWindowClosePopup(link, '');
    }
    return (
      <div className="box_menu">
          <div className="box_item" onClick={() => clickDoublePointLink('home')}>
              <div className="box_img_wrapper">
                  <img src="/img/mypage_1.svg" alt="1" />
              </div>
              <div className="box_text">
                  
              </div>
          </div>
          <div className="box_item" onClick={() => clickDoublePointLink('campaign')}>
              <div className="box_img_wrapper">
                  <img src="/img/mypage_2.svg" alt="2" />
              </div>
              <div className="box_text">
                  
              </div>
          </div>
          <div className="box_item" onClick={() => clickDoublePointLink('ichiba')}>
              <div className="box_img_wrapper box_img_wrapper_red">
                  <img src="/img/mypage_3.svg" alt="3" />
              </div>
              <div className="box_text">
                  
              </div>
          </div>
          <div className="box_item" onClick={() => clickDoublePointLink('bookmark')}>
              <div className="box_img_wrapper box_img_wrapper_red">
                  <img src="/img/mypage_4.svg" alt="4" />
              </div>
              <div className="box_text">
                  
              </div>
          </div>
          <div className="box_item" onClick={() => clickDoublePointLink('purchase_history')}>
              <div className="box_img_wrapper box_img_wrapper_red">
                  <img src="/img/mypage_5.svg" alt="5" />
              </div>
              <div className="box_text">
                  
              </div>
          </div>
      </div>
    )
}

RakutenTabs.propTypes = {
    rpp_bta: PropTypes.arrayOf(
      PropTypes.shape({
          image_click_url: PropTypes.string.isRequired,
          image_url: PropTypes.string.isRequired,
          text_click_url: PropTypes.string.isRequired,
          text: PropTypes.string.isRequired,
          price: PropTypes.number.isRequired,
      })
    ),
};
