import React, { useEffect, useState } from 'react';

window.kuchiBus = new EventTarget();

export default function KuchisuInfo() {
  const [kuchisuCount, setKuchisuCount] = useState(0);
  const [percent, setPercent] = useState(0);

  useEffect(() => {
    const handler = e => {
      const { kuchisuCount, percent } = event.detail;
      setKuchisuCount(kuchisuCount);
      setPercent(percent > 99 ? percent : percent - 2);
    }
    window.kuchiBus.addEventListener('updateKuchisu', handler);
    return () => {
      window.kuchiBus.removeEventListener('updateKuchisu', handler);
    }
  }, []);

  const listPercent = [5, 10, 15, 20, 25, 30];

  return (
    <div id="kuchiInfo">
      <div id="kuchiInfoText">
        <div></div>
        <div></div>
      </div>
      <div id="kuchiInfoLeft">
        <span className="kuchisuCount">{kuchisuCount}</span>
      </div>
      <div id="progressbar" style={{ background: `linear-gradient(to right, rgba(255, 239, 52, 1) ${percent}%, white 0%` }}>
        {listPercent.map(p => (
          <div key={p} className={(p <= kuchisuCount) ? 'progressHighlight' : null}>
            <div>{p}</div>
          </div>
        ))}
      </div>
    </div>
  );
}
