import React, {Component} from 'react';
import PropTypes from 'prop-types';
import PopupHelper from '../../../../../../utilities/src/popupHelper';

export default class RebatesCoupon extends Component {
    constructor(props){
        super(props);
        this.handleCouponClick = this.handleCouponClick.bind(this);
        this.handleStoreClick = this.handleStoreClick.bind(this);
        this.extensionHelper = new PopupHelper();
    }

    handleStoreClick(){
        this.extensionHelper.openWindowClosePopup(this.props.campaignDisplay.storeImageLink, '');
    }

    handleCouponClick(){
        this.extensionHelper.openWindowClosePopup(this.props.campaignDisplay.storeCouponUrl, '');
    }

    render(){
        return(<div id="tab-hot-deals">
                    <div className="card deal-card">
                        <div className="row">
                            <div className="col-xs-5 store-logo">
                                <a href="#" onClick={this.handleStoreClick}>
                                    <img className="store-logo-image lazy" src={this.props.campaignDisplay.storeImageLogo} data-href={this.props.campaignDisplay.storeImageLogo} alt={this.props.campaignDisplay.storeTooltip} title={this.props.campaignDisplay.storeTooltip}  /></a>
                            </div>
                            <div className="col-xs-7 text-right">
                                <a href="#" onClick={this.handleCouponClick} className="btn btn-sm shop-now-btn" rel="nofollow"></a>
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-xs-12 deal-description">
                                {this.props.campaignDisplay.description}<span className="text-rebate"> {this.props.campaignDisplay.cashBackText}</span>
                            </div>
                        </div>
                        <div className="row text-sm">
                            <div className="col-xs-12">
                                <span className="expiry-date"> {this.props.campaignDisplay.expiry}</span>
                            </div>
                        </div>
                    </div>
              </div>);
    }
}
RebatesCoupon.propTypes={
    campaignDisplay:PropTypes.object
}