import React from 'react';
import PopupHelper from '../../../../../../utilities/src/popupHelper'
import PropTypes from 'prop-types';

function UserDetailsMemberRank(props) {
  let extensionHelper = new PopupHelper();
  const getRakutenRankData = (rank) => {
    let rakutenRankStyle, rakutenMemberType, imageSrc, bgColor, textColor;
    switch (rank) {
      case '2':
        rakutenRankStyle = 'rakutenSilver rakutenRank';
        rakutenMemberType = '';
        imageSrc = 'img/silver.png';
        bgColor = '#E6E6E6';
        textColor = '#646464';
        break;
      case '3':
        rakutenRankStyle = 'rakutenGold rakutenRank';
        rakutenMemberType = '';
        imageSrc = 'img/gold.png';
        bgColor = '#FFEBA6';
        textColor = '#7A5C0B';
        break;
      case '4':
        rakutenRankStyle = 'rakutenPlatinum rakutenRank';
        rakutenMemberType = '';
        imageSrc = 'img/platinum.png';
        bgColor = '#CAEEFF';
        textColor = '#004BAA';
        break;
      case '5':
        rakutenRankStyle = 'rakutenDiamond rakutenRank';
        rakutenMemberType = '';
        imageSrc = 'img/diamond.png';
        bgColor = '#EBE0FF';
        textColor = '#674B9B';
        break;
      default:
        rakutenRankStyle = 'rakutenRegular rakutenRank';
        rakutenMemberType = '';
        imageSrc = '';
        bgColor = '#FFFFFF';
        textColor = '#333333';
        break;
    }
    return { rakutenRankStyle, rakutenMemberType, imageSrc, bgColor, textColor };
  };

  const { rakutenRankStyle, rakutenMemberType, imageSrc, bgColor, textColor } = getRakutenRankData(props.user_pointInfo.rank);

  const containerStyle = {
    position: 'relative',
    height: '62px',
    width: '100%',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'flex-start',
    backgroundColor: bgColor,
  };

  const imageStyle = {
    padding: '10px',
    width: '24px',
  };

  const textContainerStyle = {
    marginLeft: imageSrc ? '2px' : '10px', // add left margin when imageSrc is empty
    display: 'flex',
    flexDirection: 'column',
    color: textColor,
  };

  const smallTextStyle = {
    fontSize: '12px',
  };

  const bottomRightTextStyle = {
    position: 'absolute',
    bottom: '5px',
    right: '5px',
    fontSize: '12px',
    textDecoration: 'underline',
    color: '#0079B7',
    marginRight: '5px',
    cursor: 'pointer',
  };

  const boldTextStyle = {
    fontWeight: 'bold',
  };

  const handleRakutenPointClubClick = () => {
    // Add your logic here, e.g., navigate to the Rakuten Point Club page
    extensionHelper.openWindowClosePopup(`https://point.rakuten.co.jp/?scid=tb_r_crm_pointclub`, 'pointclub_link')
  };

  return (
    <div style={containerStyle}>
      {imageSrc && <img src={imageSrc} alt="User" style={imageStyle} className={rakutenRankStyle} />}
      <div style={textContainerStyle}>
        <div style={boldTextStyle}>{props.user_pointInfo.name + ""}</div>
        <div style={smallTextStyle}>{rakutenMemberType}</div>
      </div>
      <div
        style={bottomRightTextStyle}
        onClick={handleRakutenPointClubClick}
      >
        Point Club
      </div>
    </div>
  );
}

UserDetailsMemberRank.propTypes = {
  user_pointInfo: PropTypes.shape({
    rank: PropTypes.string,
    name: PropTypes.string,
  }),
};

export default UserDetailsMemberRank;
