import React from 'react';
import PropTypes from 'prop-types';

function UserDetailsPointInfo(props) {
  const style = {
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'center',
    alignItems: 'center',
  };

  const rowStyle = {
    display: 'flex',
    justifyContent: 'space-between',
    alignItems: 'center',
    width: '100%',
  };

  const leftTextStyle = {
    margin: '10px',
    fontSize: '14px',
    textAlign: 'left',
    paddingLeft: '5px',
  };

  const numberStyle = {
    margin: '10px',
    fontSize: '20px',
    fontWeight: 'bold',
    paddingRight: '5px',
    fontFamily: `"Noto Sans", "Hiragino Kaku Gothic ProN", Meiryo, sans-serif`,
  };

  const hrStyle = {
    width: '90%', // Reduce the width to 90%
    borderTop: '1px solid #EBEBEB',
    margin: 0,
    padding: 0,
  };

  return (
    <div style={style}>
      <div style={rowStyle}>
        <div style={leftTextStyle}></div>
        <div style={numberStyle}>{props.user_pointInfo.yesterdayPoint}</div>
      </div>
      <hr style={hrStyle} />
      <div style={rowStyle}>
        <div style={leftTextStyle}></div>
        <div style={numberStyle}>{props.user_pointInfo.monthlyPoint}</div>
      </div>
    </div>
  );
}

UserDetailsPointInfo.propTypes = {
  user_pointInfo: PropTypes.shape({
    yesterdayPoint: PropTypes.number,
    monthlyPoint: PropTypes.number,
  }),
};

export default UserDetailsPointInfo;