import React from 'react';
import PopupHelper from '../../../../../utilities/src/popupHelper'
import  { CONST_RAKUTEN_WEBSEARCH } from  '../../../../../utilities/src/setting_config'
import * as PropTypes from 'prop-types'

const extensionHelper = new PopupHelper();
const login_url = `${CONST_RAKUTEN_WEBSEARCH.defaultUrl}/rtoken_login.html?tool_id=1`;
const register_url = `https://login.account.rakuten.com/sso/register?client_id=rakuten_web_search_web&redirect_uri=https://websearch.rakuten.co.jp/rtoken_login?tool_id=1&scope=openid&response_type=code&state=any-string-to-keep-state#/registration/1`

function Login({ location = 'tab', style }) {
  const loginMsgClick = () => {
    extensionHelper.openWindowClosePopup(login_url, 'tbChromeWindow');
  }

  const loginClick = () => {
    extensionHelper.openWindowClosePopup(login_url, 'tbChromeWindow');
  }

  const registerMsgClick = () => {
    extensionHelper.openWindowClosePopup(register_url, 'tbChromeWindow');
  }

  return (
    <div className="login" id={`${location === 'tab' ? 'login_screen' : 'login_popup'}`} style={style}>
      <div id="loginMsg" onClick={loginMsgClick}>
        
      </div>
      <div id="login" onClick={loginClick}>
        <a href="#" className="myButton"></a>
      </div>
      <div id="loginBottomMsg" style={{ cursor: "pointer" }} onClick={registerMsgClick}>
        
      </div>
    </div>
  )
}

Login.propTypes = {
  location: PropTypes.string,
  style: PropTypes.object,
};

export default Login;
