import React, { Component } from 'react';
import PropTypes from 'prop-types';
import RebatesImage from './RebatesImage';
import WhatIsRebates from './WhatIsRebates';

export default class RebatesNonActivate extends Component {

  constructor(props) {
    super(props);
    this.state = {
      shoppingURL: this.props.rebates_info.link,
      comment: this.props.rebates_info.comment,
      domainRegex: this.props.rebates_info.domainRegex
    };
  }

  componentDidMount() {
    let shopUrl = document.getElementById('shopURL');
    if (shopUrl && this.state.domainRegex != '') {
      shopUrl.addEventListener('click', () => {
        platform_api_root.tabs.query({ active: true, currentWindow: true }, (tabs) => {
          platform_api_root.tabs.update(tabs[0].id, { url: `${this.state.shoppingURL}&deepLinkUrl=${encodeURIComponent(tabs[0].url)}` });
          window.close();
        });

        this.props.onClick();
      });
    }
  }

  render() {
    let rbFontSize = "12px";
    if(!this.props.rebates_info.fixedCashBack){
      rbFontSize ="11px";
    }

    const styles = {
      bannerButtonStyle: {
        'fontSize': rbFontSize
      }
    }

    return (
      <div>
        {
          this.state.domainRegex == "" ?
          <div /> :
          <div>
          <hr />
          <div className="RebatesMerchantsSite">
            <RebatesImage activated={false} />
            <button id='shopURL' style={styles.bannerButtonStyle} className="RebatesMerchantsSiteBeforeActivateBtn">{this.state.comment}GET</button>
          </div>
          <WhatIsRebates rebates_info={this.props.rebates_info}/>
          </div>
        }
      </div>
    );
  }
}

RebatesNonActivate.propTypes = {
  rebates_info: PropTypes.object,
  onClick: PropTypes.func
};